/* Basics
-----------------------------------------*/

body {
    font-size: rem-calc(14);
    line-height: 1.875;
	
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

p {
	font-size: rem-calc(14);
    line-height: rem-calc(28);
    margin: 0 0 1em;
}

a, 
a:visited {
    text-decoration: none;
    color: inherit;
    transition: $transition-all;
    
    .icon {
        position: relative;
        top: rem-calc(-2);
        left: rem-calc(0);
        transition: left $t-time $bezier;
    }
    
    &:hover,
    &:focus {
        .icon {
            left: rem-calc(5);
        }
    }
}

main a {
    position: relative;
    z-index: 0;
    
    &:before {
        content: '';
        width: 100%;
        position: absolute;
        height: 4px;
        background: $light-grey;
        top: 50%;
        z-index: -1;
        transition: $transition-all;
    }
    
    &:hover,
    &:focus {
        color: $text-colour--neg;
        &:before {
            top: 0;
            height: 100%;
            background: $black;
        }
    }
}

.noclass {
    display: block;
}

@media ($from-medium) {
    
}
@media ($from-medium) {
    
    body,
    p {
        font-size: rem-calc(16);
        line-height: rem-calc(36);
    }
}

@media ($from-xlarge) {
    
    body,
    p {
        font-size: rem-calc(22);
    }
}

@media ($from-xxlarge) {
    
    body,
    p {
        font-size: rem-calc(30);
    }
}

.sticky {
    position: sticky!important;
    top: 20px;
}